<template>
  <el-table
    :data="tableData"
    style="width: 100%"
    v-loading="loading"
    element-loading-text="">
    <el-table-column
      type="index"
      width="50">
    </el-table-column>
    <el-table-column
      prop="userName"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      label="">
      <template slot-scope="scope">
        {{scope.row.menuType && scope.row.menuType[0]}}
      </template>
    </el-table-column>
    <el-table-column
      label="">
      <template slot-scope="scope">
        {{scope.row.menuType && scope.row.menuType[1]}}
      </template>
    </el-table-column>
    <el-table-column
      prop="createdAt"
      label=""
      width="220">
    </el-table-column>
    <el-table-column
      prop="others"
      label="">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'HistoryList',

  props: {
    // 
    tableData: {
      type: Array,
      required: true
    },
    loading: Boolean
  }
}
</script>




